.canvasWrapper{
    width: 100%;
    height: 100%;
    background-size: cover; /* Cover the entire area */
    background-position: center; /* Center the background image */
}

.canvasWrapper video{
    z-index: 1;
}

.draggableHandle{
    padding: 5px;
    border: solid rgb(175, 175, 175) 1px;
    border-radius: 3px;
}

.draggableHandle:hover{
 cursor: move;
}